@media (max-width: 767px) {
    /* Estilos para dispositivos m√≥viles */
    body {
        position: fixed;
        overflow: hidden;
    }
    body main.intro{
        display: grid;
        place-content: center;
        padding: 0;
        margin: 0;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }
    body main.intro .content-container {
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
    }
    .navbar .logo-link{
        height: 45px;
    }
    .navbar .logo-link img{
        height: 100%;
      }
    main.intro .content-container *{
        text-align: justify;
    }
    div.content-container .sub-title {
        display: grid;
        place-content: center;
        font-size: min(1.5rem, 2vw);
    }
    div.text-animated-container {
        width: fit-content;
    }
    div.content-container .title {
        font-size: clamp(2vw, 3.5em, 8vw);
    }
    div.content-container .title.span {
        font-size: clamp(1.5vw, 2.5em, 6vw);
    }
    div.content-container .description{
        display: grid;
        place-content: center;
        margin: 0;
        font-size: max(13px, 2.5vw);
    }
    div.content-container .description .animated-item{
        margin-top: 30px;
        padding:0 clamp(25px, 1rem, 10vw);
    }
    div.content-container .description{
        width: min(90vw, 500px);
    }
    .animated-item{
        width: fit-content;
    }
    div.content-container div.start-ui {
        margin: 0;
        margin-top: 20px;
        padding: 0;
        display:  grid;
        place-content: center;
    }
    div.content-container div.start-ui *{
        margin: 0;
    }
    div.content-container div.start-ui .ui-title{
        font-size: clamp(.8rem, 1rem, 3vw);
    }
    div.content-container div.ui-controls{
        width: 100vw;
        height: min(180px, 10vh);
        justify-content: center;
    }

    div.content-container div.ui-controls #start-scene{
        margin: 0;
    }
    div.content-container div.ui-controls #start-scene::after{
        font-size: clamp(.8rem, 1rem, 3vw);
        margin: 0;
        top: -20px;
        left: 50%;
        transform: translate(-50%, 0);
        text-align: center;
    }
    div.content-container div.ui-controls #toggle-sound{
        position: absolute;
        margin: 0;
        top: 50%;
        right: clamp(10px, 30px, 40px);
        transform: translate(0, -50%);
    }


    /*ESTILOS PARA DOTS*/
    .content.active-section{
        width: 140vw !important;
    }
    div.content.active-section div *{
    }

    /*CONFIGURAR CADA SECTION*/
    /*HOME*/
    .home-swiper{
        width: 100%;
    }
    .home-swiper .list-container{
        flex-wrap: wrap;
    }
    .home-swiper .list-container .btn-sub-page-container{
        width: calc(100% / 2.1);
        margin: 2px 0;
        justify-content: end;
    }
    .alternative.active-section.content *{
        margin: 0;
        text-align: center;
    }

    .alternative.active-section.content{
        display: flex !important;
        align-items: center;
        justify-content: center;
    }
    .alternative.active-section.content .title{
        font-size: 30px;
    }
    .alternative.active-section.content 
    .external-links.list-container{
        justify-content: center;
        margin: 0;
    }
    /*END HOME*/
    /*BLOG*/
    .content.active-section .blog-slide-container{
        width: 110%;
        max-height: 95vh;
    }
    .content.active-section .blog-slide-container .blog-slide{
        width: 100%;
    }
    /*END BLOG*/

    /*CONTACT*/
    .content.contact-section.active-section div{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        width: 100%;
        height: auto;
        flex-direction: column;
    }
    .content.contact-section.active-section div h2{
        text-align: center;
    }
    .content.contact-section.active-section div .section{
        margin: 0;
        margin-top: 15px;
    }
    .content.contact-section.active-section div .section label{
        font-size: 15px;
    }
    .content.contact-section.active-section div .section input{
        width: 50%;
    }

    .content.contact-section.active-section div .contact-container{
        height: fit-content;
        padding-bottom: 60px;
    }

    .content.contact-section.active-section div #contact-send-btn{
        margin: 0;
        margin-top: 40px;
    }
    .content.contact-section.active-section div .contact-preview{
        display: none;
    }
    .content.contact-section.active-section div .contact-blur{
        height: 100%;
    }
    .content.contact-section.active-section div .footer-form{
        width: 100%;
    }
    .content.contact-section.active-section div .footer-form .follow-us{
        margin-top: 15px;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }
    .content.active-section .title{
        font-size: 30px;
        margin-bottom: 40px;
    }
    .content.active-section .description{
        margin: 0;
        line-height: 20px;
        font-size: 15px;
        padding: 0 30px;
    }
    .content.active-section .list-container{
        margin-top: 30px;
    }
    /*END CONTACT*/

    /*NTFS*/
    .content.ntfs-section.active-section div{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .content.ntfs-section.active-section div .title{
        width: 92%;
        font-size: 35px;
        margin-bottom: 20px !important;
    }
    .content.ntfs-section.active-section div .description{
        line-height: 20px;
        font-size: 15px;
        margin: 10px 0 !important;
    }
    .content.ntfs-section.active-section div .img-preview{
        height: 230px;
        margin-bottom: 20px !important;
    }


    .content.ntfs-section.active-section *{
        margin: 0 !important;
        text-align: center !important;
    }

    /*END NTFS*/

    /*SUB SECTIONS NETWORK*/
    .content.network-sub-page-content.active-section .description{
        padding: 0 !important;
        line-height: 20px !important;
        font-size: 20px !important;
    }
    /*END SUB SECTIONS NETWORK*/

    /*estilos de la barra de NEVEGACION*/
    div#navigator{
        padding: 0;
        justify-content: center;
    }
    div#navigator .icon img{
        height: 45px !important;
        width: 45px !important;
    }
    div#back-btn{
        width: 30px;
        height: 30px;
        margin-left: 2px;
        margin-top: 2px;
    }

    /*DOT*/
    .dot img{
        height: 120px !important;
        width:  120px !important;

    }
    .dot .icon.network-sub-page img{
        height: 80px !important;
        width:  80px !important;
    }
    /*END DOT*/
  }
  